<template>
  <div>
    <ul class="nav nav-tabs">
      <li class="nav-item">
        <a
          data-toggle="tab"
          href="#caracteristiques"
          class="nav-link"
          :class="{'active' : section == 'caractéristiques'}"
          @click.prevent="section = 'caracteristiques'"
        >Caractéristiques</a>
      </li>
      <li class="nav-item">
        <a
          data-toggle="tab"
          href="#propositions"
          class="nav-link"
          :class="{'active' : section == 'propositions'}"
          @click.prevent="section = 'propositions'"
        >Propositions</a>
      </li>
      <li class="nav-item">
        <a
          data-toggle="tab"
          href="#criteres"
          class="nav-link"
          :class="{'active' : section == 'criteres'}"
          @click.prevent="section = 'criteres'"
        >Critères</a>
      </li>
      

    </ul>
    <div class="tab-content mt-3">
      <div class="tab-pane fade show active">

        <b-container fluid v-if="section == 'caracteristiques'">
           <!--Annonce SUR LA MAP -->
          <h2 class="text-capitalize font-weight-bold fs-4">Localisation</h2>
          <b-row class="rounded text-white py-2" style="margin: 0; background: #f5365c;">
            <b-col lg="4" md="6" sm="12">
              <span class="d-inline-block w-100 my-1">
                Pays: 
                <b class="font-weight-bold">{{ annonce.logement.adresse.pays }}</b>
              </span>
            </b-col>
            <b-col lg="4" md="6" sm="12">
              <span class="d-inline-block w-100 my-1">
                Ville:
                <b class="font-weight-bold">{{ annonce.logement.adresse.ville }}</b>
              </span>
              </b-col>
              <b-col lg="4" md="6" sm="12">
              <span class="d-inline-block w-100 my-1">
                Quartier:
                <b class="font-weight-bold">{{ annonce.logement.adresse.quartier }}</b>
              </span>
            </b-col>
          </b-row>
          <b-container class="mt-4 p-0">
            <div class="map-container">
                <GmapMap
                  :center="{lat: parseFloat(annonce.logement.adresse.lat), lng: parseFloat(annonce.logement.adresse.lon)}"
                  :zoom="16"
                  class="w-100"
                  style="height:340px;"
                  >
                  <gmap-custom-marker :marker="marker">
                    <!--<img src="/img/mapMarker.png" @mouseover="showCoordonates"/>-->
                    <img src="/img/mapMarker.png" @mouseenter="showCoordonates" @mouseleave="hidecoordonates"/>
                  </gmap-custom-marker>
                </GmapMap> 
                <div v-show="dvisible" class="mini-composant">
                  <div class="internal">
                    <p>Latitude : <span>{{ latitude_5 }}</span></p>
                    <p>Longitude: <span>{{ longitude_5 }}</span></p>
                  </div>
                </div>
            </div>
        
          </b-container>
          <hr />
           <vue-easy-lightbox
            :visible="section_photo.visible"
            :imgs="photos"
            :index="section_photo.index"
            @hide="section_photo_handleHide"
          /> 
          <hr/>
          <h2 class="text-capitalize font-weight-bold fs-4 my-4">Caractéristiques approfondies</h2>
          <b-row>
            <b-col cols="12">
              <span class="d-inline-block w-100 my-1 h6">
                Titre:
                <strong>{{ annonce.titreAnnonce }}</strong>
              </span>
              <span class="d-inline-block w-100 text-muted">
                Posté le : 
                <small>{{ annonce.createdAt.slice(0, 10) }}</small>
              </span>

            </b-col>
            <b-col cols="12">
              <div>
                <b-badge
                  class="m-1 bg-badge background"
                  v-for="(c, i) in tags"
                  :key="c || i"
                  >
                  <b class="fa-lg h6">{{ c }}</b>
                </b-badge>
              </div> 
            </b-col>
         
          </b-row>
          <div class="jumbotron pt-0 pb-10 mt-2 mb-0">
            <p class="text-justify description">{{ annonce.descAnnonce }}</p>
          </div>
           <!--Annonce SUR LA MAP -->
          <h2 class="text-capitalize font-weight-bold fs-4 my-4">Photos</h2>
          <!-- <hr /> -->
          <div class="mx-auto overflow-hidden rounded current-picture">
            <img :src="currentPicture" alt="image" class="w-100 h-100">
          </div>
          <div v-if="photos.length > 1" class="d-flex justify-content-center mt-4">
            <div class="mx-1 shadow"  style="width: 3rem; height: 3rem" v-for="(photo, i) in photos" :key="i">
                <div class="card d-flex flex-row mb-4 overflow-hidden justify-content-between w-100 h-100">
                  <a
                    class="d-flex card-img rounded overflow-hidden"
                    @click.prevent="() => section_photo_showImg(i)"
                    href="#"
                  >
                    <img
                      :src="photo"
                      alt
                      class="list-thumbnail responsive border-0 w-100 h-100"
                    />
                  </a>
              </div>
            </div>
          </div>
           <vue-easy-lightbox
            :visible="section_photo.visible"
            :imgs="photos"
            :index="section_photo.index"
            @hide="section_photo_handleHide"
          />
          
          
          <h2 class="text-capitalize font-weight-bold fs-4 my-4">Informations sur le logement</h2>
          <table class="table table-hover table-dark">
            <thead>
              <tr>
                <th scope="col">Libellé</th>
                <th scope="col">Valeur</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Etat du logement</td>
                <td>
                  <b-badge v-if="!annonce.logement.etatLogement" class="mt--1 mr-2 btn bg-badge background rounded-pill">
                    <small class="fa-sm">Logement occupé</small>
                  </b-badge>
                  <b-badge v-else class="mt--1 mr-2 btn bg-badge bg-danger rounded-pill">
                    <small class="fa-sm">Logement libre</small>
                  </b-badge>
                </td>
              </tr>
              <tr>
                <td>Prix du logement</td>
                <td>{{ (annonce.logement.prixMax + annonce.logement.prixMin)/2 }} FCFA</td>
              </tr>
              <tr>
                <td>Début du logement</td>
                <td>{{ annonce.dateDeb }} </td>
              </tr>
              <tr>
                <td>Fin du logement</td>
                <td v-if="annonce.dateFin">{{ annonce.dateFin  }}</td>
                <td else>Indéterminée</td>
              </tr>
            </tbody>
          </table>
          <hr />
         
        </b-container>
        <b-container fluid v-if="section == 'propositions'">
           <!--Annonce SUR LA MAP -->
          <h2 class="text-capitalize font-weight-bold fs-4 mb-4">Propositions</h2>
          <!-- <hr /> -->
          <b-badge v-if="!annonce.logement.etatLogement" class="mt--1 mr-2 btn bg-badge background rounded-pill">
              <samll class="fa-sm">Logement occupé</samll>
          </b-badge>
          <b-badge v-else class="mt--1 mr-2 btn bg-badge bg-success rounded-pill">
              <samll class="fa-sm">Logement libre</samll>
          </b-badge>
          <b-badge class="mt--1 mr-2 btn bg-badge rounded-pill">
              <samll class="fa-sm">Prix min: {{ annonce.logement.prixMin }} FCFA</samll>
          </b-badge>
          <b-badge class="mt--1 mr-2 btn bg-badge rounded-pill">
              <samll class="fa-sm">Prix max: {{ annonce.logement.prixMax }} FCFA</samll>
          </b-badge>
          <p class="btn btn-secondary position-relative rounded-pill mt-3 mr-2">
            proposition(s)
            <span class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
              {{ annonce.nbrPropostions }}
              <span class="visually-hidden"></span>
            </span>
          </p>
          <hr />         
        </b-container>
        
      </div>
    </div>
  </div>
</template>

<script>

const php = require("phpjs");
import GmapCustomMarker from 'vue2-gmap-custom-marker';
import VueEasyLightbox from "vue-easy-lightbox";
export default {
  components: {
    VueEasyLightbox,
    GmapCustomMarker
  },
  props: {
    annonce: { type: Object, required: true }
  },
  data: () => ({
    section: "caracteristiques",
    /**
     * Données manipuler par la section des photos
     */
    marker:{
      lat:'',
      lng:''
    },
    dvisible:false,
    section_photo: {
      index: 0,
      visible: false,
      images: []
    },
    tags:[],
    currentPicture: '',
   
  }),
  computed: {
    latitude_5(){
      return Number(this.annonce.logement.adresse.lat).toFixed(5);
    },
    longitude_5(){
      return Number(this.annonce.logement.adresse.lon).toFixed(5);
    },
    photos() {
      return this.annonce.photos;
    },
    caracteristiques() {
      //return this.logement.caracteristiques
      return this.logement.caracteristiques.filter(
        elt => parseInt(elt.valeur) > 0
      );
    },
    itemsDepenses() {
      return php.array_slice(
        this.logement.depenses,
        this.offset,
        this.perPage
      );
    },
     offset() {
        return (this.currentPage * this.perPage) - this.perPage
    }
  },
  mounted() {
    this.currentPicture = this.annonce.photos[0];
    this.tags=this.annonce.tags.split(',')
    this.marker.lat=this.annonce.logement.adresse.lat;
    this.marker.lng=this.annonce.logement.adresse.lon;
  },
  methods: {

    /**
     * affiche les coordonnées de la position au survol sur le marker
     */
    showCoordonates(){
       //this.dvisible = !this.dvisible; 
       this.dvisible=true;
    },
    hidecoordonates(){
       this.dvisible=false;
    },

     /**
     * Methodes relatives a la section des photos
     */
    section_photo_showImg(index) {
      this.currentPicture = this.photos[index];
      // this.section_photo.index = index;
      // this.section_photo.visible = true;
    },

    section_photo_handleHide() {
      this.section_photo.visible = false;
    },

    section_photo_uploadImageSuccess(formData, index, fileList) {
      console.log("data", formData, index, fileList);
      // Upload image api
      // axios.post('http://your-url-upload', formData).then(response => {
      //   console.log(response)
      // })
    },
  


  }
};
</script>

<style scoped>
  .nav-item .active{
    background: #f5365c;
    color: #ffffff;
    font-weight: 600;
  }
  p.description::first-letter{
    font-size: 40px;
  }
  .background{
    background: #f5365c;
  }
  .current-picture{
      width: 24rem;
      height: 24rem;
    }

  @media (max-width: 945px) {
    .current-picture{
      width: 100% !important;
      height: 15rem !important;
    }
  }

  .map-container{
    position:relative;
  }
  .mini-composant{
    width: 125px;
    height: 60px;
    background-color: #ffffff;
    position : absolute;
    top:21%;
    left: 43%;

  }
  .mini-composant .internal1{
    width:40%;
    float :left;
  }
   .mini-composant .internal2{
    width:60%;
    float :right;
  }
</style>